header {
  position: fixed;
  height: 50px;
  width: 100%;
  top: 0;
  z-index: 9999;
  box-sizing: border-box;
  background-color: white;
  /* background-color: aquamarine; */
}
header .wrapper {
  position: relative;
  height: 100%;
  width: 80vw;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}
header .title-text {
  font-size: 18px;
  float: left;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-weight: bold;
  height: 100%;
  color: black;
}
header .title-text a {
	text-decoration: none;
}
header .title-date {
	font-size: 16px;
	float: left;
  display: -webkit-flex;
  display: flex;
	margin-left: 10px;
  margin-top: 1px;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	height: 100%;
	color: black;
}
header .nav-button {
	font-size: 16px;
	float: left;
  display: -webkit-flex;
  display: flex;
  margin-left: 9px;
  margin-right: 7px;
  margin-top: 1px;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	height: 100%;
	color: black;
}
header .language {
	font-size: 18px;
	float: right;
	height: 100%;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	color: black;
}
header .icon {
	font-size: 32px;
	text-decoration: none;
	color: #c1c2c3;
	display: none;
}
header .language a, a:visited, a:hover, a:active {
	text-decoration: none;
	color: inherit;
}
.btn-lang-image {
  margin-right: 20px;
