@-webkit-keyframes hello {
	0% {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
	10% {-webkit-transform: rotate(-10deg);transform: rotate(-10deg);}
	20% {-webkit-transform: rotate(12deg);transform: rotate(12deg);}
	30% {-webkit-transform: rotate(-10deg);transform: rotate(-10deg);}
	40% {-webkit-transform: rotate(9deg);transform: rotate(9deg);}
	50% {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
	100% {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
}

@keyframes hello {
	0% {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
	10% {-webkit-transform: rotate(-10deg);transform: rotate(-10deg);}
	20% {-webkit-transform: rotate(12deg);transform: rotate(12deg);}
	30% {-webkit-transform: rotate(-10deg);transform: rotate(-10deg);}
	40% {-webkit-transform: rotate(9deg);transform: rotate(9deg);}
	50% {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
	100% {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
}

